$(".header__mobile-hmenu").click(function() {

    const durationLines13 = 400;
    const easingLines13 = "easeInOutQuart";

    const durationLine2 = 500;
    const easingLine2 = "easeInOutBack";

    if($( this ).hasClass("active")) {
        
        anime({
            targets: ".line-1",
            rotate: 0,
            duration: durationLines13,
            bottom: 0,
            easing: easingLines13
            
        })

        anime({
            targets: ".line-2",
            opacity: 1,
            duration: durationLine2,
            width: 32,
            easing: easingLine2
        })
        
        anime({
            targets: ".line-3",
            rotate: 0,
            duration: durationLines13,
            top: 0,
            easing: easingLines13
            
        })
        
    } else {
        
        anime({
            targets: ".line-1",
            rotate: 45,
            duration: durationLines13,
            bottom: -10,
            easing: easingLines13
            
        })
        
        anime({
            targets: ".line-2",
            duration: durationLine2,
            width: 0,
            opacity: {
                value: 0,
                delay: 20
            },
            easing: easingLine2
        })
                
        
        anime({
            targets: ".line-3",
            rotate: -45,
            duration: durationLines13,
            top: -10,
            easing: easingLines13
            
        })
    }
    $( this ).toggleClass("active");
    
})